  .btn,.btn:link,.btn:visited{text-transform:uppercase;
  text-decoration:none;
  padding:1.5rem 4rem;
  display:inline-block;
  border-radius:10rem;
  -webkit-transition:all .2s;
  transition:all .2s;
  position:relative;
  font-size:1.6rem;
  border:none;
  cursor:pointer}
  .btn:hover{-webkit-transform:translateY(-3px);
  transform:translateY(-3px);
  -webkit-box-shadow:0 1rem 2rem rgba(0,0,0,0.2);
  box-shadow:0 1rem 2rem rgba(0,0,0,0.2)}
  .btn:hover::after{-webkit-transform:scaleX(1.4) scaleY(1.6);
  transform:scaleX(1.4) scaleY(1.6);
  opacity:0}
  .btn:active,.btn:focus{outline:none;
  -webkit-transform:translateY(-1px);
  transform:translateY(-1px);
  -webkit-box-shadow:0 0.5rem 1rem rgba(0,0,0,0.2);
  box-shadow:0 0.5rem 1rem rgba(0,0,0,0.2)}
  .btn--white{background-color:#fff;
  color:#777}
  .btn--white::after{background-color:#fff}
  .btn--green{background-color:#c0d175;
  color:#fff}
  .btn--green::after{background-color:#c0d175}
  .btn::after{content:"";
  display:inline-block;
  height:100%;
  width:100%;
  border-radius:10rem;
  position:absolute;
  top:0;
  left:0;
  z-index:-1;
  -webkit-transition:all .4s;
  transition:all .4s}
  .btn--animated{-webkit-animation:moveInBottom .5s ease-out .75s;
  animation:moveInBottom .5s ease-out .75s;
  -webkit-animation-fill-mode:backwards;
  animation-fill-mode:backwards}
  .btn-text:link,.btn-text:visited{font-size:1.6rem;
  color:#c0d175;
  display:inline-block;
  text-decoration:none;
  border-bottom:1px solid #c0d175;
  padding:3px;
  -webkit-transition:all .2s;
  transition:all .2s}
  .btn-text:hover{background-color:#c0d175;
  color:#fff;
  -webkit-box-shadow:0 1rem 2rem rgba(0,0,0,0.15);
  box-shadow:0 1rem 2rem rgba(0,0,0,0.15);
  -webkit-transform:translateY(-2px);
  transform:translateY(-2px)}
  .btn-text:active{-webkit-box-shadow:0 0.5rem 1rem rgba(0,0,0,0.15);
  box-shadow:0 0.5rem 1rem rgba(0,0,0,0.15);
  -webkit-transform:translateY(0);
  transform:translateY(0)}
